@import url(https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap);
body {
  font-family: 'Roboto Mono', monospace;
  background-color: #f9f9f9;
  overflow-x: hidden;
  overflow-y: auto; }
  body h1 {
    color: #14868c;
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3); }
  body h3 {
    margin-bottom: 0.8rem; }
  body .radioBox {
    display: flex;
    flex-wrap: wrap; }
    body .radioBox input[type=radio] {
      display: none; }
    body .radioBox label {
      display: inline-block;
      padding: 4px 12px;
      margin-right: 4px;
      white-space: nowrap;
      color: #14868c;
      border: 1px solid #14868c;
      border-radius: 16px;
      -webkit-transition: all .3s;
      transition: all .3s;
      cursor: pointer; }
      body .radioBox label:first-child {
        font-weight: bold; }
      body .radioBox label.checked {
        color: #fff;
        background-color: #14868c; }
  body .radioTitle {
    min-width: 120px;
    margin-right: 6px; }
  body .boxWrapper {
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.35);
    padding: 16px;
    max-width: 800px;
    min-height: 350px;
    margin: 12px auto; }
    body .boxWrapper.align-content-stretch .boxItem, body .boxWrapper.align-items-stretch .boxItem {
      height: auto; }
  body .boxItem {
    width: 90px;
    height: 90px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 6px;
    font-size: 20px;
    text-align: center; }
    body .boxItem:nth-child(5n-4) {
      background-color: indianred; }
    body .boxItem:nth-child(5n-3) {
      background-color: darkorange;
      width: 70px;
      height: 70px;
      display: flex;
      justify-content: center;
      align-items: center; }
    body .boxItem:nth-child(5n-2) {
      background-color: gold; }
    body .boxItem:nth-child(5n-1) {
      background-color: seagreen;
      width: 110px;
      height: 110px;
      display: flex;
      justify-content: center;
      align-items: center; }
    body .boxItem:nth-child(5n) {
      background-color: steelblue; }
  body .numberBox {
    display: flex;
    align-items: center; }
    body .numberBox .number {
      width: 60px;
      text-align: center; }
  body .roundBtn {
    border-radius: 50%;
    color: #fff;
    font-size: 20px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
    width: 24px;
    height: 24px;
    display: flex;
    justify-content: center;
    align-items: center; }
    body .roundBtn.minusBtn {
      background-color: indianred; }
    body .roundBtn.plusBtn {
      background-color: seagreen; }
  body .selectorWrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around; }
  body .selector {
    border: 1px solid #14868c;
    border-radius: 8px;
    margin: 6px;
    padding: 8px; }
  body .selRow {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 4px 0; }
    body .selRow:not(:last-child) {
      border-bottom: 1px solid #d8d8d8; }
  body .itemWrapper {
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.35);
    padding: 16px;
    max-width: 800px;
    min-height: 200px;
    margin: 12px auto; }
  body .itemSelf {
    width: 90px;
    height: 90px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 6px;
    font-size: 20px; }
    body .itemSelf:nth-child(5n-4) {
      background-color: indianred; }
    body .itemSelf:nth-child(5n-3) {
      background-color: darkorange; }
    body .itemSelf:nth-child(5n-2) {
      background-color: gold; }
    body .itemSelf:nth-child(5n-1) {
      background-color: seagreen; }
    body .itemSelf:nth-child(5n) {
      background-color: steelblue; }
    body .itemSelf.align-self-stretch {
      height: auto; }
  body .flex-basis-0 {
    flex-basis: 0 !important; }
  body .flex-basis-px {
    flex-basis: 100px !important; }
  body .flex-basis-percent {
    flex-basis: 50% !important; }

@media (max-width: 768px) {
  body .boxItem:nth-child(n+6) {
    display: none; }
  body .itemSelf {
    width: 70px;
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center; }
  body .selector {
    font-size: 13px;
    margin: 0;
    padding: 4px; }
    body .selector .numberBox .number {
      width: 30px; }
  body .itemSelf:nth-child(n+4), body .selector:nth-child(n+4) {
    display: none; } }

